import React, { useState, useEffect } from 'react';
import { View, Text, StyleSheet, ImageBackground } from 'react-native';

export default function AboutPage() {
  const [text, setText] = useState('');
  const message = "Know about me....";

  useEffect(() => {
    let index = 0;
    const typingInterval = setInterval(() => {
      if (index <= message.length) {
        setText(message.substring(0, index));
        index++;
      } else {
        clearInterval(typingInterval);
      }
    }, 100); 

    return () => clearInterval(typingInterval);
  }, []);
  const educationDetails = [
    {
      degree: 'Bachelor of Technology in Data Science',
      institution: 'Ramco Institute of Technology,Rajapalayam',
      date: '2021-2015',
    },
    {
      degree: '12th',
      institution: 'St. Aloysius PU College,Harihar,Karnataka',
      date: '2021',
    },
    {
        degree: '10th',
        institution: 'M.K.E.T L.K.E.M School,Harihar,Karnataka',
        date: '2019',
      },
  ];

  return (
    <ImageBackground
      source={require('../assets/home.jpg')}
      style={styles.container}
    >
      <View style={styles.content}>
        <Text style={styles.typewriterText}>{text}</Text>
        <View style={styles.space} />
        <Text style={styles.paragraph}>
          Hello, I am G. Nithyalakshmi. I am an aspiring data analyst with a strong background in data-driven decision-making and a passion for transforming raw data into actionable insights. Proficient in data analysis tools, statistical modeling, and data visualization.
        </Text>
        <View style={{height:50}}/>
        <View style={styles.educationContainer}>
          <Text style={styles.educationHeader}>Education:</Text>
          {educationDetails.map((edu, index) => (
            <View key={index} style={styles.educationItem}>
              <Text style={styles.degree}>{edu.degree}</Text>
              <Text style={styles.institution}>{edu.institution}</Text>
              <Text style={styles.date}>{edu.date}</Text>
            </View>
          ))}
        </View>
      </View>
    </ImageBackground>
  );
}
const styles = StyleSheet.create({
  container: {
    flex: 1,
    resizeMode: 'cover', 
  },
  content: {
    flex: 1,
    justifyContent: 'flex-start', 
    alignItems: 'center',
    paddingTop: 40, 
  },
  typewriterText: {
    fontSize: 24,
    fontWeight: 'bold',
    color: '#ccc',
  },
  paragraph: {
    fontSize: 16,
    color: '#ccc', 
    textAlign: 'center', 
    marginHorizontal: 20,
  },
  space: {
    height: 40,
  },
  educationContainer: {
    marginTop: 20,
  },
  educationHeader: {
    fontSize: 20,
    fontWeight: 'bold',
    color: '#fff',
    marginBottom: 10,
  },
  educationItem: {
    marginBottom: 10,
  },
  degree: {
    fontSize: 16,
    fontWeight: 'bold',
    color: '#fff',
  },
  institution: {
    fontSize: 14,
    color: '#ccc',
  },
  date: {
    fontSize: 14,
    color: '#ccc',
  },
  mapContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    marginTop: 20,
  },
  mapLabel: {
    flex: 1,
    textAlign: 'center',
    color: '#fff',
  },
  mapLine: {
    flex: 3,
    borderBottomWidth: 2,
    borderColor: '#fff',
  },
});
