import React, { useState, useEffect } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, ImageBackground } from 'react-native';


const HomePage = ({ navigation }) => {
  const navigateToAbout = () => {
    navigation.navigate('About');
  };

  const navigateToSkills = () => {
    navigation.navigate('Skills');
  };
  const navigateToProjects = () => {
    navigation.navigate('Projects');
  };
  const navigateToContact = () => {
    navigation.navigate('Contact');
  };

  const [text, setText] = useState('');
  const message = "Welcome to my portfolio";

  useEffect(() => {
    let index = 0;
    const typingInterval = setInterval(() => {
      if (index <= message.length) {
        setText(message.substring(0, index));
        index++;
      } else {
        clearInterval(typingInterval);
      }
    }, 100);

    return () => clearInterval(typingInterval);
  }, []);

  return (
    <ImageBackground
      source={require('../assets/home.jpg')} 
      style={styles.container}
    >
      {/* Top-left corner */}
      <View style={styles.topLeftContainer}>
        <TouchableOpacity onPress={navigateToAbout} style={styles.linkButton}>
          <Text style={styles.linkText}>About Me</Text>
        </TouchableOpacity>
        <TouchableOpacity onPress={navigateToSkills} style={[styles.linkButton, styles.portfolioButton]}>
          <Text style={styles.linkText}>Skills</Text>
        </TouchableOpacity>
        <TouchableOpacity onPress={navigateToProjects} style={[styles.linkButton, styles.portfolioButton]}>
          <Text style={styles.linkText}>Projects</Text>
        </TouchableOpacity>
        <TouchableOpacity onPress={navigateToContact} style={[styles.linkButton, styles.portfolioButton]}>
          <Text style={styles.linkText}>Contact</Text>
        </TouchableOpacity>
      </View>
      <View style={styles.content}>
        <Text style={styles.typewriterText}>{text}</Text>
      </View>
    </ImageBackground>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    resizeMode: 'cover', 
    justifyContent: 'center',
    alignItems: 'center',
  },
  content: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
  },
  topLeftContainer: {
    position: 'absolute', 
    top: 0, 
    left: 0, 
    flexDirection: 'column',
    justifyContent: 'flex-start',
    alignItems: 'flex-start',
    margin: 10,
  },
  linkButtonContainer: {
    flexDirection: 'column', 
    alignItems: 'flex-start', 
  },
  linkButton: {
    paddingVertical: 10,
    paddingHorizontal: 20,
    marginVertical: 5, 
  },
  linkText: {
    fontSize: 18,
    fontWeight: 'bold',
    color: '#ccc',
  },
  portfolioButton: {
    marginTop: 0,
  },
  typewriterText: {
    fontSize: 24,
    fontWeight: 'bold',
    color:'#ccc'
  },
});

export default HomePage;
